.container {
    display: flex;
	flex-wrap: nowrap;
    height: 75vh;
}

.halfPage1 {
    /* align-items: center; */
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    width: 50%;
    box-sizing: border-box;
    padding: 10px;
}

.halfPage {
    /* align-items: center; */
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    width: 50%;
    box-sizing: border-box;
    padding: 10px;
}

.productName {
    font-size: 20px;
    font-weight: bold;
}

div.pic {
    padding: 10px 10px 0 0;
}

div.description {
    padding-top: 50px;
}

div.pic img {
    width: 75%;
    border: 0;
}

.actions {
    width: 40%;
    margin: 10px;
    justify-content: center;

}

.quantity {
    margin: 5px;
}

.addToCart {
    position: relative;
    max-width: 80%;
    margin: 0px;
    padding: 0px;
    display: flex;
    min-height: 40px;
    width: 80%;
    -webkit-box-align: center;
    align-items: center;
    -webkit-box-pack: center;
    justify-content: center;
    border-radius: 24px;
    border: none;
    cursor: pointer;
    transition: background-color 0.15s ease-in-out 0s, box-shadow 0.15s ease-in-out 0s;
    user-select: none;
    text-decoration: none;
    text-align: center;
    background-color: #F15C62;
    box-shadow: transparent 0px 0px 0px 1px inset;
    color: rgb(255, 255, 255);
}

.addToCart span {
    font-size: 16px;
    font-weight: 700;
    line-height: 18px;
    letter-spacing: 0ch;
    text-transform: none;
    text-align: center;
    margin: 0px;
    padding: 0px;
    display: block;
    font-variant-ligatures: no-common-ligatures;
    max-width: 100%;
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
}