.checkoutContainer {
    display: flex;
    flex-direction: column;
	flex-wrap: nowrap;
    width: 100%;
    /* height: 75vh; */
}

.title {
    font-size: 20px;
    font-weight: bold;
}

.subtotal {
    display: flex;
	flex-direction: column;
	flex-wrap: nowrap;
	justify-content: flex-start;
	align-items: flex-end;
	align-content: stretch;
}

.subtotal table {
    width: 100%;
}

.inputItem {
    margin: 10px;
    width: 40%;
}

.actionCheckout {
    margin: 30px 10px 0px 10px;display: flex;
	flex-direction: row;
	flex-wrap: nowrap;
    justify-content: flex-end;
}

.actionCheckout button {
    width: 30%;
}